import React, { Component } from 'react';
import renderHTML from 'react-render-html';
import { siteImproveEventPush } from './helpers/siteImproveEventPush';

//------------------- Result page ------------------------------
export class ResultPage extends Component {
  constructor(props) {
    super(props);
    this.ref = React.createRef();
  }
  
  componentDidMount = () => {
    siteImproveEventPush(
      null,
      'danmark testen',
      'Load resultpage',
      `User loads resultpage for group: ${this.props.group}`
    )

    this.ref.current.querySelectorAll('a').forEach(link =>
      link.addEventListener('click', this.onClick, false)
    );
  };

  componentWillUnmount = () => {
    this.ref.current.querySelectorAll('a').forEach(link =>
      link.removeEventlistner('click', this.onClick, false)
    );
  }

  onClick = (e) => {
    const group = this.props.group;
    const text = e.target.innerText;
    siteImproveEventPush(
      null,
      'danmark testen',
      `Click ${text}`,
      `User clicked ${text} from resultpage for group: ${group}`
    )
  };

  render() {
    return (
      <div className={this.props.className} ref={this.ref}>
        {renderHTML(this.props.markup)}
      </div>
    );
	}
}
